body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.load{
    margin-left: 550px;
    margin-top: 100px;
}
header{
    display: inline-flex;
    margin-top: 40px;
}

.searchinput{
    margin-left: 200px;
    margin-top: 40px;
    min-width: 300px;
    height: 25px;
    border-radius: 10px;
    padding: 2px 3px;
}

.headertag{
    margin-left: 250px;
}

.cnt{
    color: red;
    margin-left: 20px;
}

.full-list{
    margin-left: 50px;
}

.btn{
    border: none;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin-left: 20pxכ;
    border-radius: 50%;
    cursor: pointer;
    background-color: lightblue;
    color: rgb(124, 34, 34);
}
.country-list{
    display: grid;
    margin: 20px;
    grid-template-columns: repeat(5, 230px);
    text-align: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.country-card{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    border-radius: 40%;
    cursor: pointer;
}

.country-card:hover{
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.card-img{
    border: 1px black solid;
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.country-content{
    background-color: darkgray;
}

.continent{
    background-color: lightgray;
}


.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
   
  .box {
    position: relative;
    height: 75vh;
    margin: 0 auto;
    width: 20vw;
    margin-top: calc(100vh - 85vh - 20px);
    background: #fff;
    border-radius: 10px;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #999;
    overflow: auto;
  }
   
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(40% - 35px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }

  img{
    width: 280px;
    height: 200px;
  }

  .popul{
    margin-top: 10px;
    font-size: 15px;
  }

